import React from "react";
import PropTypes from "prop-types";
import Img from "gatsby-image";
import Navigation from "./navigation";
import TinyPost from "./tinyPost";
import { Row, Col, Box } from "jsxstyle";

const Post = props => {
  let {
    title,
    date,
    path,
    coverImage,
    coverVideo,
    author,
    excerpt,
    tags,
    html,
    links,
    previousPost,
    nextPost,
  } = props;
  const previousPath = previousPost && previousPost.frontmatter.path;
  const previousLabel = previousPost && previousPost.frontmatter.title;
  const nextPath = nextPost && nextPost.frontmatter.path;
  const nextLabel = nextPost && nextPost.frontmatter.title;

  return (
    <Col width="100%" alignItems="center" justifyContent="flex-start">
      <Box
        position="relative"
        padding="48px 0px 12px 0px"
        borderBottom="1px solid #CCC"
        marginBottom="24px"
        mediaQueries={{
          sm: "screen and (max-width: 900px)"
        }}
        smWidth="100%"
        width="900px"
      >
        <Box fontFamily="Domaine" fontSize="40px">
          {title}
        </Box>
        <Box fontFamily="EB Garamond" fontSize="24px">
          {date}
        </Box>
        <Box color="#AAA" fontFamily="EB Garamond" fontSize="24px" marginBottom="24px">
          {tags.join(", ")}
        </Box>
      </Box>
      <div className="post-detail-container" dangerouslySetInnerHTML={{ __html: html }} />
      <Box
        mediaQueries={{
          sm: "screen and (max-width: 900px)"
        }}
        smWidth="100%"
        width="900px"
      >
        <Navigation
          previousPath={previousPath}
          previousLabel={previousLabel}
          nextPath={nextPath}
          nextLabel={nextLabel}
        />
      </Box>
    </Col>
  );
};

export default Post;
